export const PropscoreRating = ({
  rating,
  className,
  width = 260,
  height = 48,
}: {
  rating: number
  className?: string
  width?: number
  height?: number
}) => {
  let calculateFilledPercent = (rating / 5) * 100
  return (
    <div className={cn("relative", className)}>
      <RatingUnfilled width={width} height={height} />
      <div
        className={cn("absolute inset-0 overflow-hidden whitespace-nowrap")}
        style={{ width: `${calculateFilledPercent}%` }}
      >
        <RatingFilled width={width} height={height} />
      </div>
    </div>
  )
}
